.nav-container{
    display: flex;
    height: 72px;
    align-items: center;
    background: #1d2e2e;
    margin: 0;
    box-shadow: 0 1px 5px rgba(0,0,0,0.12),0 1px 2px rgba(0,0,0,0.26);
    justify-content: space-between;
    z-index : 99;
}

.nav-left{
    display: flex;
    align-items : center;
}

.nav-right{
    margin-right: 40px;
}

.flash-logo{
    margin-left: 20px;
    height: 40px;
}
.flash-logo-text{
    font-family: "Bangers",cursive;
    font-size: 60px;
    letter-spacing: 1px;
    margin:0 10px;
    color: #e7e6e7;
}

.nav-developer-link{
    font-size: 18px;
    color: #e7e6e7;
    font-weight : 800;
}
